<template>
  <link
    href="https://maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css"
    rel="stylesheet"
  />
  <div class="container bootstrap snippets bootdey">
    <h2>Total : {{ Object.keys(notes).length }}</h2>
    <div class="row">
      <a href="/add"
        ><span id="post">Post </span><i class="fa fa-plus-square"></i
      ></a>
      <ul v-for="postIt in notes.reverse()" :key="postIt.id" class="notes">
        <ChildComponent
          :title="postIt.title"
          :content="postIt.content[0]"
          :id="postIt._id"
        />
      </ul>
    </div>
  </div>
</template>

<script>
//import Paginate from "vue-pagination-2";
import { mapActions, mapGetters } from "vuex";
import ChildComponent from "../components/PostitItem.vue";
export default {
  components: {
    ChildComponent,
    // Paginate,
  },
  data() {
    return { postIts: {}, itemsPerPage: 20, currentPage: 1 };
  },
  computed: {
    ...mapGetters(["notes"]),
    displayedItems() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.notes.slice(start, end);
    },
  },
  methods: {
    ...mapActions(["fetchNotes"]),
    getNotes() {
      this.postIts = this.fetchNotes();
    },
    pageClickHandler(pageNum) {
      this.currentPage = pageNum;
    },
  },
  created() {
    this.getNotes();
  },
  mounted() {
    setInterval(() => {
      this.getNotes();
    }, 30000);
  },
};
</script>
