<template>
  <li>
    <div class="lazur-bg div">
      <!-- <h4 style="color: red">{{ title }}</h4> -->
      <h4>{{ truncateText(title, 20) }}</h4>
      <p>{{ truncateText(content, 140) }}</p>
      <RouterLink :to="'/note/' + id" class="pull-right"
        ><i class="fa fa-arrow-right"></i
      ></RouterLink>
      <RouterLink :to="'/edit/' + id" class="pull-center"
        ><i class="fa fa-pencil"></i
      ></RouterLink>
      <RouterLink to="" @click="deleteNoteById({ id })" class="pull-left"
        ><i class="fa fa-trash-o"></i
      ></RouterLink>
    </div>
  </li>
</template>
<script>
// eslint-disable-next-line no-unused-vars
import { mapActions } from "vuex";
//import { RouterLink, RouterView } from "vue-router";
export default {
  props: {
    title: String,
    content: String,
    id: String,
  },
  methods: {
    truncateText(str, maxLength) {
      if (str.length > maxLength) {
        return str.slice(0, maxLength) + "...";
      } else {
        return str;
      }
    },
    ...mapActions(["deleteNote"]),
    deleteNoteById(id) {
      this.deleteNote(id.id);
    },
    async deleteData(id) {
      try {
        const response = await fetch(
          "https://post-it-7m1i.onrender.com/notes/" + id.id,
          {
            method: "DELETE",
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        const data = await response.json();
        return data;
      } catch (error) {
        console.error(error);
      }
    },
  },
};
</script>
